<template>
  <nav class="navDocument">
    <transition name="slide-fade" mode="out-in" appear>
      <div id="selectBtn" v-if="toolbar">
        <v-tooltip top>
          <template v-slot:activator="{ on }">
            <v-btn
              class="mx-1"
              color="primary"
              dark
              v-on="on"
              large
              @click.stop="goUserList"
            >
              <v-icon>mdi-arrow-left-circle</v-icon>
            </v-btn>
          </template>
          <span>回病歷管理</span>
        </v-tooltip>

        <v-tooltip top>
          <template v-slot:activator="{ on }">
            <v-btn
              class="mx-1"
              color="primary"
              dark
              v-on="on"
              large
              @click.stop="drawer_left = !drawer_left"
            >
              <v-icon>mdi-view-list</v-icon>
            </v-btn>
          </template>
          <span>病歷選擇</span>
        </v-tooltip>

        <v-tooltip top>
          <template v-slot:activator="{ on }">
            <v-btn
              class="mx-1"
              color="primary"
              dark
              v-on="on"
              large
              @click="showUser"
            >
              <v-icon>mdi-badge-account-horizontal</v-icon>
            </v-btn>
          </template>
          <span>病歷內容</span>
        </v-tooltip>

        <viewUser
          :items="userItems"
          @getALLUser="getALLUser"
          ref="editProcess"
          v-show="false"
        ></viewUser>

        <v-tooltip top>
          <template v-slot:activator="{ on }">
            <v-btn
              class="mx-1"
              color="primary"
              dark
              v-on="on"
              large
              @click.stop="drawer = !drawer"
            >
              <v-icon>mdi-apps</v-icon>
            </v-btn>
          </template>
          <span>表單選擇</span>
        </v-tooltip>
        <!-- <router-link to="/main/userlist" tag="button" class="ml-2">
              <v-icon>mdi-clipboard-account</v-icon>回病歷管理
            </router-link>-->
      </div>
    </transition>

    <v-bottom-sheet v-model="drawer">
      <v-sheet class="text-center" height="auto">
        <v-btn class="mt-6 error white--text" block @click="drawer = !drawer"
          ><v-icon>mdi-close</v-icon> close</v-btn
        >
        <div class="py-3">
          <h3 class="grey lighten-2">各評估表單</h3>
          <v-divider></v-divider>
          <v-chip
            v-for="(item, index) in filterItems"
            :key="index"
            :to="item.route"
            :color="item.color"
            class="ma-2 white--text"
            @click="drawer = !drawer"
            >{{ item.title }}</v-chip
          >
        </div>

        <v-divider></v-divider>

        <div v-if="!$store.state.pData.pharmacist_key">
          <h3 class="grey lighten-2">衛福部全人評估表單</h3>
          <v-divider></v-divider>
          <v-chip
            v-for="(item, index) in mohwItems"
            :key="index"
            :to="item.route"
            :color="item.color"
            class="ma-2 white--text"
            @click="drawer = !drawer"
            >{{ item.title }}</v-chip
          >
        </div>

        <v-divider></v-divider>

        <div v-if="!$store.state.pData.pharmacist_key">
          <h3 class="grey lighten-2">評估指標表單</h3>
          <v-divider></v-divider>
          <v-chip
            v-for="(item, index) in EvaluationMetricsItems"
            :key="index"
            :to="item.route"
            :color="item.color"
            class="ma-2 white--text"
            @click="drawer = !drawer"
            >{{ item.title }}</v-chip
          >
        </div>
      </v-sheet>
    </v-bottom-sheet>

    <!-- 病歷一覽及選擇 -->
    <v-navigation-drawer
      app
      temporary
      class="indigo"
      v-model="drawer_left"
      left
      width="25vw"
    >
      <v-list-item-title class="text-center white--text my-2"
        >病歷選擇</v-list-item-title
      >
      <v-divider color="white"></v-divider>

      <v-card>
        <v-card-text class="mt-2">
          <v-toolbar dense>
            <v-text-field
              hide-details
              prepend-icon="mdi-magnify"
              single-line
              label="搜尋"
              v-model="serachKey"
            ></v-text-field>
          </v-toolbar>

          <div style="overflow: auto">
            <v-list class="mt-2" height="77vh">
              <v-list-item-group v-model="model" color="primary">
                <v-list-item
                  v-for="(item, i) in searchfilter"
                  :key="i"
                  @click="getUserData(item)"
                >
                  <v-list-item-icon class="mr-2">
                    <v-icon>mdi-account</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      >{{ item.name }} 床號:{{
                        item.bed_name
                      }}</v-list-item-title
                    >
                    <v-list-item-subtitle
                      >病歷號:{{ item.p_code }}</v-list-item-subtitle
                    >
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </div>
        </v-card-text>
      </v-card>
    </v-navigation-drawer>
  </nav>
</template>

<script>
import viewUser from "@/views/main/UserAdd";

export default {
  components: { viewUser },
  data() {
    return {
      toolbar: false, //左下方功能列顯示/隱藏控制
      personnel_key: true,
      drawer: false,
      drawer_left: false,
      userItems: [], //病歷資料暫存
      serachKey: "",
      model: 1,
      documentItems: [
        {
          title: "就醫及藥品紀錄維護",
          route: "/main/document/medicinerecordlist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "生命徵象紀錄單",
          route: "/main/document/signlifelist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "住民定期評估表",
          route: "/main/document/recordlist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "柯氏/巴氏(ADL)量表",
          route: "/main/document/kblist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "跌倒危險因子評估表",
          route: "/main/document/falllist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "Braden壓力性損傷風險評估表",
          route: "/main/document/bradenlist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "護理計畫單",
          route: "/main/document/nursingplanlist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "入院護理評估",
          route: "/main/document/nursingassessment",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "護理紀錄",
          route: "/main/document/nursingrecordlist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "計價單",
          route: "/main/document/pricelist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "管路使用紀錄",
          route: "/main/document/pipelinelist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "檢驗報告單",
          route: "/main/document/inspectionreportlist",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "簡易心智狀態SPMSQ",
          route: "/main/document/spmsqlist",
          color: "warning",
          authKey: "pass",
        },
        {
          title: "72小時適應評估紀錄表",
          route: "/main/document/hour72",
          color: "warning",
          authKey: "pass",
        },
        {
          title: "住民社會心理認知功能評估表",
          route: "/main/document/sociopsychologicallist",
          color: "warning",
          authKey: "pass",
        },
        {
          title: "個案服務紀錄表",
          route: "/main/document/servicerecordlist",
          color: "warning",
          authKey: "pass",
        },
        {
          title: "憂鬱量表(GDS)",
          route: "/main/document/melancholylist",
          color: "warning",
          authKey: "pass",
        },
        {
          title: "適應性評估表",
          route: "/main/document/adaptabilitylist",
          color: "warning",
          authKey: "pass",
        },
        {
          title: "個案服務處遇計畫表",
          route: "/main/document/treatmentlist",
          color: "warning",
          authKey: "pass",
        },
        {
          title: "復健評估表",
          route: "/main/document/rehabilitationlist",
          color: "success",
          authKey: "pass",
        },
        {
          title: "72小時營養篩檢表",
          route: "/main/document/hour72nutrition",
          color: "pink",
          authKey: "pass",
        },
        {
          title: "營養評估單",
          route: "/main/document/nutritionlist",
          color: "pink",
          authKey: "pass",
        },
        {
          title: "轉介照會單",
          route: "/main/document/consultationlist",
          color: "pink",
          authKey: "pass",
        },
        {
          title: "72小時藥師評估表",
          route: "/main/document/pharmacistevaluationlist",
          color: "grey",
          authKey: "pharmacist",
        },
        {
          title: "用藥評估紀錄表",
          route: "/main/document/medicationevaluationlist",
          color: "grey",
          authKey: "pharmacist",
        },
      ],
      //衛福部表單
      mohwItems: [
        {
          title: "健康習慣",
          route: "/main/document/HealthyHabits",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "疾病史",
          route: "/main/document/MedicalHistories",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "藥物安全性評估",
          route: "/main/document/DrugSafeties",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "身體評估",
          route: "/main/document/BodyEvaluations",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "壓力性損傷危險評估",
          route: "/main/document/PressureInjuries",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "跌倒危險性評估",
          route: "/main/document/FallRisks",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "日常生活功能評估",
          route: "/main/document/ADLs",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "工具性日常生活活動功能評估",
          route: "/main/document/IADLs",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "認知功能評估",
          route: "/main/document/Dementias",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "情緒問題評估",
          route: "/main/document/GeriatricDepressionScales",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "簡易營養評估",
          route: "/main/document/MNASFs",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "疼痛評估",
          route: "/main/document/PainEvaluations",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "衰弱評估",
          route: "/main/document/SOFs",
          color: "primary",
          authKey: "pass",
        },
      ],
      //六大評估分析
      EvaluationMetricsItems: [
        {
          title: "跌倒事件分析登記表",
          route: "/main/document/FallEvent",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "壓力性損傷登記表",
          route: "/main/document/PressureEvent",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "感染登記表",
          route: "/main/document/InfectionEvent",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "約束登記表",
          route: "/main/document/ConstraintEvent",
          color: "primary",
          authKey: "pass",
        },
        {
          title: "非計畫性轉急性住院登記表",
          route: "/main/document/UnplannedEvent",
          color: "primary",
          authKey: "pass",
        },
      ],
    };
  },

  computed: {
    searchfilter() {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.userItems);
    },
    //2021.8.26 加入藥師權限限制. -> 只能顯示藥師相關畫面 目前為pharmacistevaluationlist
    filterItems() {
      if (this.$store.state.pData.pharmacist_key) {
        return this.documentItems.filter((i) => i.authKey == "pharmacist");
      } else {
        return this.documentItems;
      }
    },
  },

  mounted() {
    this.getSessionStorage(); //取得暫存的廠商及使用者資料
    this.getALLUser();

    setTimeout(()=>{
      this.toolbar = true
    },500)
  },

  watch: {
    searchfilter(val) {
      val.filter((i, index) => {
        if (i.snkey == this.$store.state.uData.snkey) {
          this.model = index;
        }
      });
    },
  },

  methods: {
    showUser() {
      this.$refs.editProcess.$emit("fromParent", this.$store.state.uData);
    },
    getUserData(item) {
      // console.log(item);
      sessionStorage.setItem("uData", JSON.stringify(item));
      this.$store.commit("setuData", item);
      this.$emit("reloadPage"); //透過上層頁面重刷資料
      this.drawer_left = false;
    },
    getALLUser() {
      //取得所有病歷資料
      this.$api.options("user/getAll/" + this.$store.state.databaseName).then(
        (rs) => {
          //暫存一份到store裡備用 其他頁面會使用的到
          // this.$store.commit('setData',
          // {
          //   tableName: 'user',
          //   data: rs
          // });

          this.userItems = rs
            .filter((i) => !!i.bed_name)
            .sort(function (a, b) {
              var a = a.bed_name;
              var b = b.bed_name;

              if (a === b) {
                return 0;
              } else if (a === null) {
                return 1;
              } else if (b === null) {
                return -1;
              } else {
                return a > b ? 1 : -1;
              }
            });
          // this.sortBybedname();
        }
      );
    },

    // sortBybedname() {
    //   //依id排序
    //   this.userItems = this.userItems.sort(function (a, b) {
    //     var a = a.bed_name;
    //     var b = b.bed_name;

    //     if (a === b) {
    //       return 0;
    //     } else if (a === null) {
    //       return 1;
    //     } else if (b === null) {
    //       return -1;
    //     } else {
    //       return a > b ? 1 : -1;
    //     }
    //   });
    // },

    getSessionStorage() {
      //判斷登入情況...
      this.$store.commit(
        "setuData",
        JSON.parse(sessionStorage.getItem("uData"))
      );
    },
    goUserList() {
      //回病歷管理
      this.$router.push("/main/userlist");
    },
  },
};
</script>

<style lang="scss">
.navDocument {
  #selectBtn {
    position: fixed;
    left: 10px;
    bottom: 10px;
    z-index: 100;
  }
}
</style>