<template>
  <div class="document" id="create">
    <!-- <NavbarUser /> -->
    <NavbarDocument @reloadPage="reloadPage"></NavbarDocument>
    <!-- <h1>Document</h1> -->
    <router-view v-if="loadingPage"></router-view>
  </div>
</template>

<script>
import NavbarDocument from "@/components/NavbarDocument";
export default {
  components: { NavbarDocument },
  data: () => ({
    loadingPage: true,
  }),

  created() {
    //讀入員工資料備用;目前使用為各畫面上傳資料到衛福部時需要比對之用
    this.$api.options(
      "general/getAll/" + this.$store.state.databaseName + "/personnel_nosql"
    ).then((rs) => {
      let data = rs.map(i=>{
        return {
          ...JSON.parse(i.datalist),
          snkey:i.snkey,
        }
      })
      this.$store.commit("setData", {
        tableName: "personnel",
        data: data,
      });
    });
  },

  methods: {
    reloadPage() {
      this.loadingPage = false;
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      setTimeout(() => {
        this.loadingPage = true;
        // this.$store.commit("setLoadingOff"); //讀取畫面off
      }, 1000);
    },
  },
};
</script>